import React from 'react';
import Spinner from '../components/spinner.component';
import BookList from '../components/book-list.component';

class SearchPage extends React.Component {

    state = {
        scrollPosition: 0
    }

    toggleScrollListener = (boolean) => {
        if (boolean) {
            console.log('Popup unmounted')
            window.addEventListener("scroll", this.handleScroll);
        } else {
            console.log('Popup mounted')
            window.removeEventListener('scroll', this.handleScroll);
        }

    }

    componentDidMount() {
        window.addEventListener("scroll", this.handleScroll);
        if(this.props.isHomePage === false) {
            return;
        } else {
            this.props.setHomePage(false);
        } 


    }
    
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = (e) => {
        this.setState({scrollPosition: Math.ceil(e.srcElement.documentElement.scrollTop)})
        // console.log(Math.ceil(e.srcElement.documentElement.scrollTop));
    }

    render() {
        const {books, loading} = this.props;
        const spinner = loading ? <Spinner/> : null;
        const content = !loading ? <BookList toggleScrollListener={this.toggleScrollListener} books={books} loading={loading} scrollPosition={this.state.scrollPosition}/> : null;
        return (
            <React.Fragment>
                <section className="search-page">
                    <div className="container">
                        <div className="search-page__block">
                        {spinner}
                        {content}
                        </div>
                    </div>
                </section>
            </React.Fragment>
        )
    }
}

export default SearchPage;